<template>
  <van-list
    v-model="loading"
    :finished="finished"
    finished-text="没有更多了"
    @load="onLoad"
  >
    <div v-for="(item, index) in value" :key="index">
      <slot v-bind:item="item"></slot>
      <!-- <slot v-bind:item="item">{{ item }}</slot> -->
    </div>
  </van-list>
</template>

<script>
// import { getUserById } from '@/api/user'
export default {
  name: 'LoadingList',
  props: {
    fn: {
      type: Function,
      required: true
    },
    value: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      // user: {}, // 用户信息
      // list: [], // 列表数据
      loading: false, // 控制上拉加载更多的 loading
      finished: false, // 控制是否加载结束了
      page: 1 // 获取下一页数据页码
    }
  },
  created() {
    // this.loadUser()
  },
  methods: {
    // async loadUser() {
    //   try {
    //     const { data } = await getUserById(this.$route.params.userId)
    //     this.user = data.data
    //   } catch (error) {
    //     this.$toast('获取用户数据失败')
    //   }
    // },
    async onLoad() {
      // 1. 请求获取数据
      const { data } = await this.fn({
        page: this.page, // 可选的，默认是第 1 页
        per_page: 20 // 可选的，默认每页 10 条
      })

      // 2. 把数据添加到列表中
      // list []
      // data.data.results []
      // ...[1, 2, 3] 会把数组给展开，所谓的展开就是一个一个的拿出来
      const { results } = data.data
      this.value.push(...results)
      // this.value.push(JSON.parse(JSON.stringify(...results)))
      // this.list.push(...results)
      console.log(results)
      // 3. 加载状态结束
      this.loading = false

      // 4. 判断数据是否全部加载完毕
      if (results.length) {
        this.page++ // 更新获取下一页数据的页码
      } else {
        this.finished = true // 没有数据了，不需要加载更多了
      }
    }
  }
}
</script>

<style lang="less" scoped></style>
