<template>
  <div class="user-container">
    <!-- 导航栏 -->
    <van-nav-bar
      class="page-nav-bar"
      left-arrow
      :title="user.name"
      @click-left="$router.back()"
      fixed
    ></van-nav-bar>
    <!-- /导航栏 -->

    <!-- 用户信息 -->
    <!--  v-if="user.id" 保证拿到数据 user 后再渲染 防止报错 -->
    <div class="user-info" v-if="user.id">
      <div class="base-info">
        <van-image class="avatar" round fit="cover" :src="user.photo" />
        <div class="right-area">
          <div class="stats-wrap">
            <div class="stats-item">
              <span class="count">{{ user.art_count }}</span>
              <span class="text">发布</span>
            </div>
            <div class="stats-item">
              <span class="count">{{ user.follow_count }}</span>
              <span class="text">关注</span>
            </div>
            <div class="stats-item">
              <span class="count">{{ user.fans_count }}</span>
              <span class="text">粉丝</span>
            </div>
            <div class="stats-item">
              <span class="count">{{ user.like_count }}</span>
              <span class="text">获赞</span>
            </div>
          </div>
          <follow-user v-model="user.is_following" :user-id="user.id" />
        </div>
      </div>
      <div class="label-info">
        <div v-if="user.is_media">
          <span>认证：</span>
          <span>{{ user.certi }}</span>
        </div>
        <div>
          <span>简介：</span>
          <span>{{ user.intro }}</span>
        </div>
      </div>
    </div>

    <!-- 文章列表 -->
    <loading-list class="list" :fn="getArticlesByUser" v-slot="{ item }">
      <article-item :article="item" />
    </loading-list>
  </div>
</template>

<script>
import { getUserById } from '@/api/user'
import LoadingList from '@/components/loading-list'
import FollowUser from '@/components/follow-user'
import ArticleItem from './components/article-item'
import { getArticlesByUser } from '@/api/article'
export default {
  name: 'UserIndex',
  components: {
    LoadingList,
    FollowUser,
    ArticleItem
  },
  props: {
    userId: {
      type: [Number, String, Object],
      required: true
    }
  },
  data() {
    return {
      user: {}, // 用户信息
      getArticlesByUser: getArticlesByUser.bind(null, this.userId)
    }
  },
  computed: {},
  watch: {},
  created() {
    this.loadUser()
    // 清除 layout 的缓存，让它重新渲染
    this.$store.commit('removeCachePage', 'LayoutIndex')
  },
  mounted() {
    this.$store.commit('addCachePage', 'UserIndex')
  },
  methods: {
    async loadUser() {
      try {
        const { data } = await getUserById(this.userId)
        this.user = data.data
      } catch (err) {
        this.$toast('获取用户数据失败')
      }
    }
  },
  beforeRouteLeave(to, from, next) {
    // 导航离开该组件的对应路由时调用
    // 可以访问组件实例 `this`
    // 如果不是去 文章详情 页面，则清除缓存
    const { name: pageName } = this.$options
    if (to.name !== 'article') {
      this.$store.commit('removeCachePage', pageName)
    }
    next()
  }
}
</script>

<style scoped lang="less">
.user-container {
  .user-info {
    position: fixed;
    top: 92px;
    left: 0;
    right: 0;
    background-color: #fff;
    padding: 25px 32px;
    margin-bottom: 20px;
    .base-info {
      display: flex;
      margin-bottom: 25px;
      .avatar {
        width: 155px;
        height: 155px;
        margin-right: 62px;
      }
    }
    .label-info {
      font-size: 28px;
    }
  }
  .right-area {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    .stats-wrap {
      display: flex;
      justify-content: space-between;
      .stats-item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .count {
          font-size: 26px;
          color: #0d0a10;
        }
        .text {
          font-size: 21px;
          color: #9c9b9d;
        }
      }
    }
    .follow-btn {
      width: 289px;
      height: 55px;
      line-height: 55px;
      background-color: #6bb5ff;
      color: #fff;
      border: none;
    }
  }
  .list {
    position: fixed;
    top: 380px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: auto;
  }
}
</style>
