<template>
  <van-button
    v-if="value"
    round
    size="small"
    @click="onFollow"
    :loading="loading"
  >
    <slot>{{ '已关注' }}</slot>
  </van-button>
  <van-button
    v-else
    type="info"
    color="#3296fa"
    round
    size="small"
    icon="plus"
    @click="onFollow"
    :loading="loading"
    >关注</van-button
  >
</template>

<script>
import { addFollow, deleteFollow } from '@/api/user'
export default {
  name: 'FollowUser',
  // model: {
  //   prop: 'isFollowed',
  //   event: 'update-is_followed'
  // },
  props: {
    value: {
      type: Boolean,
      required: true
    },
    // isFollowed: {
    //   type: Boolean,
    //   required: true
    // },
    userId: {
      type: [Number, String, Object],
      required: true
    }
  },
  data() {
    return {
      loading: false
    }
  },
  created() {},
  methods: {
    async onFollow() {
      this.loading = true // 展示关注按钮的 loading 状态
      try {
        if (this.value) {
          // if (this.isFollowed) {
          // 已关注，取消关注
          await deleteFollow(this.userId)
        } else {
          // 没有关注，添加关注
          await addFollow(this.userId)
        }
        // 更新视图状态
        // 子组件不能直接改父组件数据
        // this.article.is_followed = !this.article.is_followed

        this.$emit('input', !this.value)
        // this.$emit('update-is_followed', !this.isFollowed)
        // this.$emit('update-is_followed')
        // this.$emit('update:isFollowed', !this.isFollowed)
      } catch (err) {
        let message = '操作失败，请重试！'
        if (err.response && err.response.status === 400) {
          message = '不能关注自己！'
        }
        this.$toast(message)
      }
      this.loading = false // 关闭关注按钮的 loading 状态
    }
  }
}
</script>

<style lang="less" scoped></style>
